import { Component, OnInit, Output, Input, OnChanges, EventEmitter } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message, ConfirmationService, } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute } from '@angular/router';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { coopProjectDetailQuery } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/bean/project-protocol-query.bean';
import { CoopProjectTypeListQueryBean } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-search/bean/project-main-manage-search.bean';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { ProductManageHttpService } from '../../../../../product-manage/http/product-manage.http.service';

@Component({
    selector: "subject-details",
    templateUrl: './subject-details.component.html',
    styleUrls: ['./subject-details.component.scss'],
    providers: [ConfirmationService, Commfunc, ProductManageHttpService, ProjectHttpService]
})

export class SubjectDetails implements OnInit {
    videoheader: string;
    processId: any;
    videoVal: any = {};
    videoShow: boolean;
    mainFlowNo: any;
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    bean: coopProjectDetailQuery = new coopProjectDetailQuery();
    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean();
    startTime: any;
    sateType: any;
    personName: any;
    detailDisplay: boolean = false;
    startDate: Date = null;
    productId: any;
    endDate: Date = null;
    proName: any;
    ch: any = CALENDAR_CN;
    msgs: any;
    dataMag: any = {};
    type1: any;
    projectId: any;
    options1: any[] = [];
    methods: any[] = [];
    method: any = '01';
    changeType: any = '01';
    code: any;
    details: any;
    coopName: any;
    list1: any;
    accdisplay: boolean = false;
    detaillist: any;
    constructor(
        public confirmationService: ConfirmationService,
        public productPlatformHttpService: ProductManageHttpService,
        public router: Router,
        public commfunc: Commfunc,
        public http: ProjectHttpService,
        public activatedRoute: ActivatedRoute,
    ) {
    }
    ngOnInit() {
        console.log(this.inValue)
        if (this.inValue && this.inValue != '') { //客群那边的传值
            this.bean.projectId = this.inValue.projectId;
            this.processId = this.inValue.processId;
            this.type1 = '2';
        } else {
            debugger;
            if (this.activatedRoute.snapshot.params.from == 'reminder-do') { //审批session取值
                let t = this.commfunc.getSessionDataCH('cooperative-project-from-reminder-do');
                t = JSON.parse(t);
                this.processId = t['processId'];
                let dataMap = JSON.parse(t['dataMap']);
                console.log(dataMap)
                this.details = dataMap;
                this.bean.projectId = this.details['projectId'];
                this.type1 = '1';
                if (this.activatedRoute.snapshot.params.mainFlowNo) {
                    this.mainFlowNo = this.activatedRoute.snapshot.params.mainFlowNo;
                }
            } else { //路由传值
                this.bean.projectId = this.activatedRoute['snapshot']['queryParams']['projectId'];
                this.processId = this.activatedRoute['snapshot']['queryParams']['processId'];
            }
        }
        this.queryList();
        this.codeValues();
    }
    queryList() {
        this.msgs = [];
        this.bean.callSeviceFlag = '0';
        this.http.coopProjectDetailQuery(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.dataMag = data;
                this.detaillist = this.dataMag['elementGroupList'];
                this.copyData()
                this.query();
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    // 影像查看
    lookvideo() {
        this.videoShow = true;
        this.videoheader = '影像资料查看';
        this.videoVal = {
            custNo: this.dataMag['custId'],
            custName: this.dataMag['custName'],
            processId: this.processId,
            QueryFlag: 1,
            falg: 1,
        }
        console.log(this.videoVal)
    }
    //
    copyData() {
        let datalist = this.dataMag['elementGroupList'];
        let newData = {};
        let ss = [];
        for (let i = 0; i < datalist.length; i++) {
            if (datalist[i].rowNum > 1) {
                let key = datalist[i]['elementEnName'];
                if (datalist[i].rowNum == 2) {
                    newData[key] = []
                    newData[key].push(datalist[i - 2].elementValue);
                }
                newData[key].push(datalist[i].elementValue);
            }
        }
        for (let key in newData) {
            newData[key] = newData[key].join(',');
            this.dataMag['elementGroupList'].forEach((b, i) => {
                let key1 = b['elementEnName'];
                if (key1 == key) {
                    if (b.rowNum == 2) {
                        b['elementValue'] = newData[key];
                        b['elementDesc'] = newData[key];
                    } else {
                        ss.unshift(i);
                    }
                }
            })
        }
        ss = ss.sort(function (a, b) { return b - a; });
        ss.forEach(u => {
            u = Number(u);
            this.dataMag['elementGroupList'].splice(u, 1);
        })
    }
    goback() {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/ProjectProtocolQuery'], { queryParams: { index: 1 } })
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
    query() {
        this.msgs = [];
        this.coopProjectTypeListQueryBean.pageSize = 20000;
        this.coopProjectTypeListQueryBean.pageNum = 1;
        this.coopProjectTypeListQueryBean.coopProjectTypeId = this.dataMag['coopProjectTypeId']
        this.http.coopProjectTypeListQuery(this.coopProjectTypeListQueryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // this.coopName = data['returnList']['coopProjectTypeName']
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });

        })
    }
    click() {
        let arr = this.dataMag['protocolContent'].split('/');
        let path = '';
        for (let j = 1; j < arr.length - 1; j++) {
            path += '/' + arr[j];
        }
        path = path + '/';
        let donwname = arr[arr.length - 1];
        open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(donwname) + "&fileUrl=" + path, '_blank');
    }

    kezhang() {
        let ret = {};
        this.detaillist.forEach(f => {
            if (f.elementEnName == 'coreSettlementAccount' && f.elementValue != '') {
                ret['account'] = f.elementValue;
                this.inValue = ret
                this.accdisplay = true;
            }
        })
    }
}